    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    border-radius: 49px;
}

.product > img{
    height: 170px;
    border-radius:auto;

}

.product__info > p{
    margin-bottom: 8px;
    
}

.info__price{
    font-weight: bold;

}
.info__name{
    font-size: 1rem;
